<template>
  <main class="subpage">
    <!-- -->
    <div class="container">
      <!---->
      <aside id="lnb">
        <ul>
          <li class="on">
            <router-link to="/course">Courses</router-link>
          </li>
        </ul>
      </aside>
      <!-- -->
      <div class="content">
        <h2 class="sub_title"><span>Courses</span></h2>
        <div class="inner course">
          <div class="box">
            <div class="items">
              <h3 class="subtitle">this is what I do</h3>
            </div>
          </div>
          <div class="box">
            <div class="items">
              <img src="../assets/img/about/about01.jpg" alt="about01">
              <h4>BODY BUILDING</h4>
              <p>Heres everything a beginner needs to know about natural bodybuilding. A question nearly every beginner asks: how many hours of training should you average a week to become bigger and more defined? The answer is this: it really depends on your training splits.</p>
            </div>
            <div class="items">
              <img src="../assets/img/about/about02.jpg" alt="about02">
              <h4>MUSCLE GAIN</h4>
              <p>More strength is more muscle. The heavier the weights you lift, the stronger your body becomes, and the bigger your muscles grow. Your muscles increase in size so they can lift heavier weights. This is why strength is size  lift heavy weights and youll gain muscle mass naturally.</p>
            </div>
            <div class="items">
              <img src="../assets/img/about/about03.jpg" alt="about03">
              <h4>WEIGHT LOSS</h4>
              <p>Exercise, while not required to lose weight, can help you lose weight more quickly. Lifting weights has particularly good benefits.
                By lifting weights, youll burn lots of calories and prevent your metabolism from slowing down, which is a common side effect of losing weight.</p>
            </div>
            <div class="items">
              <img src="../assets/img/about/about04.jpg" alt="about04">
              <h4>Outdoor Fitness</h4>
              <p>This is the ideal course for any personal trainer looking to conduct exercise outside the gym environment. Many clients, particularly during summertime, love to train outdoors. This course aims to provide additional skills and knowledge to help create and deliver safe and effective exercise sessions to groups of people in outdoor environments.</p>
            </div>
            <div class="items">
              <img src="../assets/img/about/about05.jpg" alt="about05">
              <h4>Advanced Stretching</h4>
              <p>Stretching is a fundamental part of any workout programme and is essential for well-rounded fitness. On this course youll build on the knowledge you gained from your personal trainer course and other fitness qualification(s) and explore advanced stretching techniques. This includes equipment stretches, a wider range of stretching types and more specific progressions.</p>
            </div>
            <div class="items">
              <img src="../assets/img/about/about06.jpg" alt="about06">
              <h4>Indoor Cycling</h4>
              <p>Bring your energy into the studio and boost your earning potential with group indoor cycling.
                Our Group Indoor Cycling course has been designed to offer you the skills needed to plan and deliver varied, fun and effective indoor cycling classes.
                The course is recognised by the Register of Exercise Professionals(REPS).</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<!--script -->
<script>
export default {
  name: 'course-component',
  data () {
    return {
      text: 'this is course component'
    }
  }
}
</script>

