<template>
  <header id="header">
    <h1 id="top_logo">
      <router-link to="/">ZACSON</router-link>
    </h1>
    <!--gnb-->
    <nav id="top_nav">
      <ul>
        <li v-for="(list, index) in menuList" v-bind:key="list.text">
          <router-link v-bind:to="list.alink">{{list.text}}</router-link>
          <!--company submenu-->
          <ul v-if="index == 0" class="sub_menu">
            <!--v-for   router-link  -->
            <li v-for="(list, index) in companySubmenuList">
              <router-link v-bind:to="list.alink">{{list.text}}</router-link>
            </li>
          </ul>
          <!--community submenu-->
          <ul v-if="index == 3" class="sub_menu">
            <li v-for="(list, index) in communitySubmenuList">
              <router-link v-bind:to="list.alink">{{list.text}}</router-link>
            </li>
          </ul>
        </li>
      </ul>
    </nav>
  </header>
</template>

<!--script -->
<script>
export default {
  name: 'header-component',
  data () {
    return {
      menuList: [
        {text: 'About', alink: '/about'},
        {text: 'Courses', alink: '/course'},
        {text: 'Gallery', alink: '/gallery'},
        {text: 'Contact', alink: '/contact'}
      ],
      companySubmenuList: [
        {text: 'About', alink: '/about'},
        {text: 'Pricing', alink: 'pricing'},
        {text: 'Blog', alink: '/blog'}
      ],
      communitySubmenuList: [
        {text: 'Contact', alink: '/contact'},
        {text: 'Faq', alink: '/faq'}
      ]
    }
  }
}
</script>

<!--style sheat -->
<style lang="scss">//scss   lang="scss" 
</style>



